import { Component, ChangeDetectionStrategy, ViewChild, ElementRef, OnInit } from '@angular/core';
import { ViewportScroller } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { delay } from 'rxjs/operators';
import { uniqId } from '@bp/shared';

@Component({
	selector: 'bp-product-page',
	templateUrl: './product-page.component.html',
	styleUrls: ['./product-page.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProductPageComponent implements OnInit {

	background = 'product';

	routingImgSrc = require('./routing.png');

	calendarImgSrc = require('./calendar.png');
	calendarImgRetinaSrc = require('./calendar@2x.png');

	dashboardImgSrc = require('./dashboard.png');
	cashierImgSrc = require('./cashier.png');
	depositButtonImgSrc = require('./deposit-button.png');

	@ViewChild('cashierWidgetOutlet') cashierOutlet: ElementRef<HTMLDivElement>;

	constructor(private scroller: ViewportScroller, private route: ActivatedRoute) {
		this.route.fragment
			.pipe(delay(100))
			.subscribe(it => this.scroller.scrollToAnchor(it));
	}

	ngOnInit() {
		this.mountCashierWidget();
	}

	private mountCashierWidget() {
		const $script = document.createElement('script');
		$script.src = 'https://embed-sandbox.bridgerpay.com/cashier';
		$script.dataset.cashierKey = '98f40a87-5a05-4c9f-895d-817ef8b3d655';
		$script.dataset.cashierToken = '';
		$script.dataset.country = 'CY';
		$script.dataset.currency = 'USD';
		$script.dataset.currencyLock = 'true';
		$script.dataset.orderId = uniqId('website');
		this.cashierOutlet.nativeElement.appendChild($script);
	}
}
